import { memo, useState, useEffect } from 'react';
import { navigate } from '@reach/router';
import { makeStyles } from '@material-ui/core/styles';
import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import Typography from '@material-ui/core/Typography';
import { navItems } from '../configs';

const useStyles = makeStyles((theme) => ({
  item: {
    '&:hover': {
      backgroundColor: theme.colors.sidebarHighLight
    }
  },
  title: { fontSize: 14 },
  subTitle: { fontSize: 10 }
}));

function Nav() {
  const classes = useStyles();
  const location = window.location;
  const [tab, setTab] = useState(0);

  function onChange(ev, val) {
    ev.preventDefault();
    navigate(navItems[val].key);
    setTab(val);
  }

  useEffect(() => {
    navItems.forEach((item, index) => {
      if (window.location.pathname.includes(item.key)) {
        setTab(index);
      }
    });
  }, [location]);

  return (
    <Tabs
      value={tab}
      indicatorColor="primary"
      textColor="primary"
      onChange={onChange}
      aria-label="disabled tabs example"
    >
      {navItems.map((item) => {
        const { key, labelCN, labelEN } = item;
        return (
          <Tab
            className={classes.item}
            key={key}
            label={
              <>
                <Typography component="p" color="primary" className={classes.title}>
                  {labelCN}
                </Typography>
                <Typography component="p" color="primary" className={classes.subTitle}>
                  {labelEN}
                </Typography>
              </>
            }
          />
        );
      })}
    </Tabs>
  );
}

export default memo(Nav);
