import { memo } from 'react';
import { Router } from '@reach/router';
import Loadable from '@loadable/component';
import { makeStyles } from '@material-ui/core/styles';
import NotFound from './NotFound';

const AsyncHome = Loadable(() => import('./Home'));

const AsyncJoin = Loadable(() => import('./Join'));

const AsyncPartners = Loadable(() => import('./Partners'));

const AsyncAbout = Loadable(() => import('./About'));

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: theme.spacing(10)
  }
}));

function Content() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Router primary={false}>
        <AsyncHome path="/" />
        <AsyncAbout path="about" />
        <AsyncJoin path="join" />
        <AsyncPartners path="partners" />
        <NotFound default />
      </Router>
    </div>
  );
}

export default memo(Content);
